<template>
  <div id="containts">
    <div id="activeChat">
      <div id="chatHistory">
        <h2>Messages</h2>
        <div id="text">
          <ul>
            <li
              v-for="message in messages"
              :key="message.id"
              v-bind:class="[me === message.author.id ? 'ownerM' : '']"
            >
              {{ message.text }}|{{ message.author.username }}@{{
                message.timestamp
              }}
            </li>
          </ul>
        </div>
        <div id="interact">
          <input v-model="newMessage" placeholder="Your text here" />
          <button @click="sendMessage" name="Send">Send</button>
        </div>
      </div>
      <div id="chatInfo">
        <h2>Info</h2>
        <h3>{{ room.name }}</h3>
        <button id="exitBtn" @click="exit">Exit</button>
        <ul id="membersList">
          <li
            v-for="member in members"
            :key="member.id"
            v-bind:class="[me === member.id ? 'ownerL' : '']"
          >
            {{ member.username }}
          </li>
        </ul>
        <div v-if="isOwner">
          <input v-model="newName" placeholder="Change name" />
          <button @click="change()">Change</button>
          <button @click="deleteRoom()" name="del">Delete chat</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  CURRENT_ROOM_CHANGED,
  DELETE_ROOM,
  MEMBER_JOINED,
  MEMBER_LEFT,
  MESSAGE_CREATED,
  SEND_MESSAGE,
  UPDATE_ROOM
} from "../constants/graphql";

export default {
  name: "ActiveChat",
  props: ["room"],
  data() {
    return {
      members: this.room.members,
      messages: this.room.lastMessages,
      me: this.$parent.me.id,
      newMessage: "",
      newName: ""
    };
  },
  computed: {
    isOwner() {
      return this.$parent.me.id === this.room.owner.id;
    }
  },
  apollo: {
    $subscribe: {
      messageCreated: {
        query: MESSAGE_CREATED,
        result({ data }) {
          const { messageCreated } = data;
          console.log(messageCreated);
          if (messageCreated.room.id.localeCompare(this.room.id) === 0) {
            this.messages.push(messageCreated);
          }
        }
      },
      memberJoined: {
        query: MEMBER_JOINED,
        result({ data }) {
          const { newMember } = data;
          this.members.push(newMember);
        }
      },
      memberLeft: {
        query: MEMBER_LEFT,
        result({ data }) {
          const { oldMember } = data;
          for (let member in this.members) {
            if (parseInt(member.id) === parseInt(oldMember.id)) {
              const index = this.members.indexOf(oldMember.id);
              this.members.splice(index, 1);
            }
          }
        }
      },
      currentRoomChanged: {
        query: CURRENT_ROOM_CHANGED,
        result({ data }) {
          const { changedMember } = data;
          for (let member in this.members) {
            if (parseInt(member.id) === parseInt(changedMember.id)) {
              member = changedMember;
            }
          }
        }
      }
    }
  },
  methods: {
    async sendMessage() {
      await this.$apollo.mutate({
        mutation: SEND_MESSAGE,
        variables: {
          message: this.newMessage
        }
      });
    },
    async change() {
      const result = await this.$apollo.mutate({
        mutation: UPDATE_ROOM,
        variables: {
          id: this.room.id,
          name: this.newName
        }
      });
      console.log(result);
      this.room.name = result.data.updateRoom.name;
    },
    async deleteRoom() {
      this.exit();
      await this.$apollo.mutate({
        mutation: DELETE_ROOM,
        variables: {
          id: this.room.id
        }
      });
      
    },
    exit() {
      this.$apollo.subscriptions.messageCreated.stop();
      this.$apollo.subscriptions.memberJoined.stop();
      this.$apollo.subscriptions.memberLeft.stop();
      this.$apollo.subscriptions.currentRoomChanged.stop();
      console.log(this.$apollo.getClient());
      console.log("may be stopped");
      this.$emit("exit");
    }
  }
};
</script>

